





























import { Vue, Component } from 'vue-property-decorator';

import { IComponent } from '@/../models';

// component
import TreeBasic from '@/views/components/tree/tree_basic.vue';
import TreeBasicSelect from '@/views/components/tree/tree_basic_select.vue';
import TreeRedefinestructure from '@/views/components/tree/tree_redefine_structure.vue';
import TreeDragDrop from '@/views/components/tree/tree_drag_drop.vue';
import TreeIcon from '@/views/components/tree/tree_icon.vue';
import TreeEdit from '@/views/components/tree/tree_edit.vue';

@Component({
    components: { TreeBasic, TreeBasicSelect, TreeRedefinestructure, TreeDragDrop, TreeIcon, TreeEdit },
})
export default class VuePageClass extends Vue {
    private treeData: IComponent.ITreeData[] = [];

    private treeOptions: IComponent.ITreeOptions = {
        checkbox: true,
    };

    private beforeMount(): void {
        // basic use with state
        let stateUseData: IComponent.ITreeData[] = [
            { text: 'Item 1' },
            { text: 'Item 2', state: { expanded: false }, children: [{ text: 'Item 2.1' }, { text: 'Item 2.2' }, { text: 'Item 2.3' }] },
            { text: 'Item 3', state: { selected: true } },
            {
                text: 'Item 4',
                children: [{ text: 'Item 4.1' }, { text: 'Item 4.2' }, { text: 'Item 4.3' }],
                state: {
                    selected: false,
                    selectable: true,
                    checked: false,
                    expanded: false,
                    disabled: false,
                    visible: true,
                    indeterminate: false,
                    matched: false,
                    editable: true,
                    dragging: false,
                    draggable: true,
                    dropable: true,
                },
            },
        ];
        // this.treeDataState = stateUseData;

        // redefine data
        let redefineData: any = this.getTreeData();
        this.treeData = redefineData;
    }

    private getTreeData(): any {
        this.treeOptions.propertyNames = {
            text: 'MY_TEXT',
            children: 'KIDS',
        };
        return [
            {
                MY_TEXT: 'JS: The Right Way',
                state: { expanded: true },
                KIDS: [
                    { MY_TEXT: 'Getting Started', state: { checked: true } },
                    { MY_TEXT: 'JavaScript Code Style', state: { selected: true } },
                    {
                        MY_TEXT: 'The Good Parts',
                        KIDS: [
                            { MY_TEXT: 'OBJECT ORIENTED', state: { checked: true } },
                            { MY_TEXT: 'ANONYMOUS FUNCTIONS', state: { checked: true } },
                            { MY_TEXT: 'FUNCTIONS AS FIRST-CLASS OBJECTS', state: { checked: true } },
                            { MY_TEXT: 'LOOSE TYPING', state: { checked: true } },
                        ],
                    },
                    {
                        MY_TEXT: 'Patterns',
                        KIDS: [
                            {
                                MY_TEXT: 'DESIGN PATTERNS',
                                state: { expanded: true },
                                KIDS: [
                                    {
                                        MY_TEXT: 'Creational Design Patterns',
                                        KIDS: [{ MY_TEXT: 'Factory' }, { MY_TEXT: 'Prototype' }, { MY_TEXT: 'Mixin' }, { MY_TEXT: 'Singleton' }],
                                    },
                                    { MY_TEXT: 'Structural Design Patterns' },
                                ],
                            },
                            {
                                MY_TEXT: 'MV* PATTERNS',
                                cildren: [{ MY_TEXT: 'MVC Pattern' }, { MY_TEXT: 'MVP Pattern' }, { MY_TEXT: 'MVVM Pattern' }],
                            },
                        ],
                    },
                ],
            },
        ];
    }
}
