




























import { Vue, Component } from 'vue-property-decorator';

import { IComponent } from '@/../models';

// component
import Card from '@/../components/card/card.vue';
import TreeComponent from '@/../components/tree/tree.vue';

@Component({
    components: { Card, TreeComponent },
})
export class TreeEdit extends Vue {
    private treeData: IComponent.ITreeData[] = [];

    private treeOptions: IComponent.ITreeOptions = {
        checkbox: true,
        dnd: true,
        // minFetchDelay: 1000,
    };

    private data: IComponent.ITreeData[][] = [
        [{ text: 'Who Should Read This Book?' }, { text: 'How to Read This Book' }, { text: 'What’s in This Book?' }, { text: 'Have Fun!' }],
        [
            { text: 'Naming Variables' },
            { text: 'Creating New Variables Using Math', state: { disabled: true } },
            { text: 'Incrementing and Decrementing' },
            { text: '+= (plus-equals) and –= (minus-equals)' },
        ],
        [
            { text: 'Interactive Programming' },
            {
                text: 'Find the Buried Treasure!',
                state: { disabled: true, expanded: true },
                children: [
                    { text: '#1: A Snowman-Drawing Function' },
                    { text: '#2: Drawing an Array of Points' },
                    { text: '#3: Painting with Your Mouse' },
                    { text: '#4: Drawing the Man in Hangman' },
                ],
            },
            { text: 'Object-Oriented Programming' },
        ],
    ];

    private async beforeMount(): Promise<void> {
        // redefine data
        let redefineData: any = this.getTreeData();
        this.treeOptions.fetchData = (node) => Promise.resolve(this.data[node.id - 1]);
        this.treeData = redefineData;
    }

    private getTreeData(): { text: string; isBatch: boolean; id: number }[] {
        return [
            {
                text: 'Introduction',
                isBatch: true,
                id: 1,
            },
            {
                text: 'Part I: Fundamentals',
                isBatch: true,
                id: 2,
            },
            {
                text: 'Data Types and Variables',
                isBatch: true,
                id: 3,
            },
        ];
    }

    private editNode(node: any): void {
        console.log('editNode => ', node);
    }

    private removeNode(node: any): void {
        console.log('removeNode => ', node);
    }

    private addNode(node: any): void {
        console.log('addNode => ', node);
    }
}
export default TreeEdit;
Vue.component('tree-edit', TreeEdit);
