


























import { Vue, Component } from 'vue-property-decorator';

import { IComponent } from '@/../models';

// component
import Card from '@/../components/card/card.vue';
import TreeComponent from '@/../components/tree/tree.vue';

@Component({
    components: { Card, TreeComponent },
})
export class TreeDragDrop extends Vue {
    private treeData: IComponent.ITreeData[] = [];

    private treeOptions: IComponent.ITreeOptions = {
        checkbox: true,
        dnd: true,
    };

    private beforeMount(): void {
        // redefine data
        let redefineData: any = this.getTreeData();
        this.treeData = redefineData;
    }

    private getTreeData(): any {
        this.treeOptions.propertyNames = {
            text: 'MY_TEXT',
            children: 'KIDS',
        };
        return [
            {
                MY_TEXT: 'Company ( not draggable )',
                state: { expanded: true, state: { draggable: false } },
                KIDS: [
                    {
                        MY_TEXT: 'Department I ( not draggable )',
                        state: { checked: true, draggable: false },
                        KIDS: [{ MY_TEXT: 'Employee 1' }],
                    },
                    {
                        MY_TEXT: 'Department II',
                        KIDS: [
                            {
                                MY_TEXT: 'Employee 2',
                                state: { expanded: true },
                                KIDS: [
                                    { MY_TEXT: 'Employee 3' },
                                    { MY_TEXT: 'Employee 4' },
                                    { MY_TEXT: 'VIP 1 ( not draggable )', state: { draggable: false } },
                                    { MY_TEXT: 'VIP 2 ( not draggable )', state: { draggable: false } },
                                ],
                            },
                        ],
                    },
                    {
                        MY_TEXT: 'Super Department I',
                        state: { expanded: true },
                        KIDS: [
                            {
                                MY_TEXT: 'Department I',
                                state: { expanded: true },
                                KIDS: [
                                    {
                                        MY_TEXT: 'Employee 5',
                                        state: { expanded: true, checked: true },
                                        KIDS: [
                                            {
                                                MY_TEXT: 'Employee 6',
                                                KIDS: [{ MY_TEXT: 'Employee 7' }, { MY_TEXT: 'Employee 8' }],
                                            },
                                            { MY_TEXT: 'Employee 9' },
                                        ],
                                    },
                                    { MY_TEXT: 'Employee 10' },
                                ],
                            },
                            {
                                MY_TEXT: 'Department II',
                                KIDS: [{ MY_TEXT: 'Employee 11', KIDS: [{ MY_TEXT: 'Employee 12' }, { MY_TEXT: 'Employee 13' }] }],
                            },
                        ],
                    },
                ],
            },
        ];
    }

    private dragStart(node: string): void {
        console.log('dragStart => ', node);
    }

    private dragFinish(targetNode: string): void {
        console.log('dragFinish => ', targetNode);
    }
}
export default TreeDragDrop;
Vue.component('tree-drag-drop', TreeDragDrop);
