




























import { Vue, Component } from 'vue-property-decorator';

import { IComponent } from '@/../models';

// component
import Card from '@/../components/card/card.vue';
import TreeComponent from '@/../components/tree/tree.vue';

@Component({
    components: { TreeComponent, Card },
})
export class TreeBasic extends Vue {
    private treeData: IComponent.ITreeData[] = [];

    private treeOptions: IComponent.ITreeOptions = {
        checkbox: true,
    };

    private beforeMount(): void {
        // basic use
        this.treeData = [
            {
                text: 'JS: The Right Way',

                // it makes node expanded
                state: { expanded: true },
                children: [
                    { text: 'Getting Started', state: { checked: true } },
                    { text: 'JavaScript Code Style', state: { selected: true } },
                    {
                        text: 'The Good Parts',
                        children: [
                            { text: 'OBJECT ORIENTED', state: { checked: true } },
                            { text: 'ANONYMOUS FUNCTIONS', state: { disabled: true } },
                            { text: 'FUNCTIONS AS FIRST-CLASS OBJECTS', state: { checked: true } },
                            { text: 'LOOSE TYPING', state: { disabled: true } },
                        ],
                    },
                    {
                        text: 'Patterns',
                        children: [
                            {
                                text: 'DESIGN PATTERNS',
                                state: { expanded: true },
                                children: [
                                    {
                                        text: 'Creational Design Patterns',
                                        children: [{ text: 'Factory' }, { text: 'Prototype' }, { text: 'Mixin' }, { text: 'Singleton' }],
                                    },
                                    { text: 'Structural Design Patterns' },
                                ],
                            },

                            {
                                text: 'DESIGN PATTERNS',
                                state: { expanded: true },
                                children: [{ text: 'MVC Pattern' }, { text: 'MVP Pattern' }, { text: 'MVVM Pattern' }],
                            },
                        ],
                    },
                ],
            },
        ];
    }
}
export default TreeBasic;
Vue.component('tree-basic', TreeBasic);
