














































































































import { Vue, Component, Prop, Watch } from 'vue-property-decorator';

import { IComponent } from '@/../models';

// tree
import LiquorTree from 'liquor-tree';
Vue.use(LiquorTree);

@Component({
    components: { LiquorTree },
})
export class TreeComponent extends Vue {
    @Prop({
        type: Boolean, // Boolean, Number, String, Array, Object
        default: () => true,
    })
    private showFilter: boolean;

    @Prop({
        type: Boolean, // Boolean, Number, String, Array, Object
        default: () => false,
    })
    private editable: boolean;

    @Prop({
        type: Boolean, // Boolean, Number, String, Array, Object
        default: () => false,
    })
    private showIcon: boolean;

    @Prop({
        type: Array, // Boolean, Number, String, Array, Object
        default: () => [],
    })
    private treeData: IComponent.ITreeData[];

    @Prop({
        type: Object, // Boolean, Number, String, Array, Object
        default: () => {},
    })
    private treeOptions: IComponent.ITreeOptions;

    @Watch('showFilter', { immediate: true, deep: true })
    private showFilterChanged(newVal: boolean, oldVal: boolean): void {}

    @Watch('editable', { immediate: true, deep: true })
    private editableChanged(newVal: boolean, oldVal: boolean): void {}

    @Watch('showIcon', { immediate: true, deep: true })
    private showIconChanged(newVal: boolean, oldVal: boolean): void {}

    @Watch('treeData', { immediate: true, deep: true })
    private treeDataChanged(newVal: any[], oldVal: any[]): void {}

    @Watch('treeOptions', { immediate: true, deep: true })
    private treeOptionsChanged(newVal: object, oldVal: object): void {}

    private filter: string = '';

    private async beforeMount() {}

    //////////////////////////////// drag ////////////////////////////////
    private dragStart(node: any): void {
        this.$emit('drag-start', node.text);
    }
    private dragFinish(targetNode: any): void {
        this.$emit('drag-finish', targetNode.text);
    }

    //////////////////////////////// edit ////////////////////////////////
    private showEdit(node): boolean {
        return node.enabled();
    }

    private editNode(node, e): void {
        if (node.enabled()) {
            node.startEditing();
            this.$emit('edit-node', node);
        }
    }

    private removeNode(node): void {
        if (node.enabled()) {
            if (confirm(this.$i18n.Dialog_ConfirmDelete)) {
                node.remove();
                this.$emit('remove-node', node);
            }
        }
    }

    private addChildNode(node): void {
        if (node.enabled()) {
            node.append(this.$i18n.Button_Create);
            this.$emit('add-node', node);
        }
    }
}
export default TreeComponent;
Vue.component('tree-component', TreeComponent);
