





























import { Vue, Component } from 'vue-property-decorator';

import { IComponent } from '@/../models';

// component
import Card from '@/../components/card/card.vue';
import TreeComponent from '@/../components/tree/tree.vue';

@Component({
    components: { TreeComponent, Card },
})
export class TreeIcon extends Vue {
    private treeData: IComponent.ITreeData[] = [];

    private treeOptions: IComponent.ITreeOptions = {
        checkbox: false,
    };

    private beforeMount(): void {
        // basic use
        this.treeData = [
            {
                text: 'Social',
                data: {
                    icon: 'fas fa-folder',
                    text: 'Social',
                },
                state: { expanded: true },
                children: [
                    {
                        text: 'Twitter',
                        data: {
                            icon: 'fab fa-twitter-square',
                        },
                    },
                    {
                        text: 'Facebook',
                        data: {
                            icon: 'fab fa-facebook-square',
                        },
                    },
                    {
                        text: 'Google+',
                        data: {
                            icon: 'fab fa-google-plus-square',
                            text: 'Google+',
                        },
                    },
                ],
            },

            {
                text: 'Currency',
                data: {
                    icon: 'fas fa-folder',
                    text: 'Social',
                },
                children: [
                    {
                        text: 'Euro',
                        data: {
                            icon: 'fas fa-euro-sign',
                            text: 'Euro',
                        },
                    },
                    {
                        text: 'Bitcoin',
                        data: {
                            icon: 'fab fa-btc',
                            text: 'Bitcoin',
                        },
                    },
                    {
                        text: 'Yen',
                        data: {
                            icon: 'fas fa-yen-sign',
                            text: 'Yen',
                        },
                    },
                ],
            },
        ];
    }
}
export default TreeIcon;
Vue.component('tree-icon', TreeIcon);
